    showOption(value) {
      return this.productInfo.filter((i) => i.title === value)[0].isActive;
    },
    chooseOption(item) {
      this.productInfo.map((i) => {
        const result = i;
        result.isActive = i.title === item.title;
        return result;
      });
    },
  },
